.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-contacts {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
}